.profile-page {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden; /* 防止滚动条出现 */

  /* 使用伪元素添加背景图像 */
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url('../../assets/Today is a beautiful day.png'); /* 确保路径正确 */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.38; /* 设置背景图像的透明度 */
    z-index: -1; /* 确保背景在内容之下 */
  }
}

.profile-content-wrapper {
  display: flex;
  flex: 1;
  padding-top: 60px; /* 留出 Header 空间 */
 
}

.profile-sider {
  width: 300px;
  background-color: transparent;
  padding: 16px;
  border-right: 1px solid #ddd;
}

.profile-avatar {
  text-align: center;
  margin-bottom: 16px;
}

.profile-avatar img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.profile-content {
  flex: 1;
  padding: 20px;
  background: transparent;
 
}

.profile-sider .ant-menu-item {
  font-size: 16px;
}

  