.mention-page {
  padding: 60px 20px 20px; /* 增加顶部内边距以向下平移 */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.main-content {
  display: flex;
  width: 100%;
  max-width: 1200px;
  margin-top: 20px;
  gap: 20px;
}

.left-section {
  flex: 2;
  display: flex;
  flex-direction: column;
}

.right-section {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.post-container {
  display: flex;
  flex-direction: column;
  gap: 30px; /* 增加间距 */
  border-right: 1px solid #ddd; /* 分界线 */
  padding-right: 20px; /* 与右侧标签部分分隔 */
}

.post-wrapper {
  width: 100%;
  max-width: 600px; /* 确保宽度一致 */
}

.add-post {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #fff;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid #ddd; /* 添加下边框 */
}

.textarea-container {
  position: relative;
  width: 100%;
}

.add-post textarea {
  width: calc(100% - 80px); /* 留出按钮空间 */
  height: 120px;
  padding: 12px;
  border-radius: 8px;
  border: 1px solid #ddd;
  resize: none;
  font-size: 16px;
  box-sizing: border-box;
}

.post-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  width: 70px;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
  position: absolute;
  right: 15px;
  bottom: 15px;
}

.post-button:hover {
  background-color: #0056b3;
}

.mention-content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center; /* 水平居中对齐 */
  width: 100%;
  max-width: 600px; /* 确保与其他内容宽度一致 */
  padding-top: 30px; /* 向下平移帖子部分 */
}

.posts-list {
  background: #fff;
  border-radius: 10px;
  padding: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  border-top: 1px solid #ddd; /* 添加上边框 */
}

.posts-list p {
  margin: 0; /* 去除默认的段落边距 */
}

.post-item {
  width: 100%;
  max-height: 200px;
  overflow: auto;
  border-bottom: 1px solid #eee;
  padding: 15px;
  margin-bottom: 15px;
  box-sizing: border-box;
  border-radius: 8px;
  background: #fafafa;
}

.post-item:last-child {
  border-bottom: none;
}

.pagination-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.right-section .tags,
.right-section .ranking {
  padding: 15px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px; /* 增加标签和排行榜之间的间距 */
}

.right-section .tags h3,
.right-section .ranking h3 {
  margin-top: 0;
  margin-bottom: 10px;
}

.tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.tag-item {
  background: #f0f0f0;
  border-radius: 15px;
  padding: 8px 12px;
  font-size: 14px;
  color: #333;
}

.ranking ol {
  margin: 0;
  padding: 0;
  list-style-type: decimal;
}

.ranking li {
  margin-bottom: 8px;
  font-size: 14px;
  color: #333;
}



